import { SvgLayer } from "./PB_SvgLayer";
import * as d3 from "d3-selection";
import { StateManager } from "./PB_StateManager";
import * as G from "./PB_GlobeVar";
import { Device } from "./PB_Device";
import { ContextMenu } from "./PB_ContextMenu";

/////////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
////----------------------------------------------------------------------------------------------------------////
export interface Point {
  x: number;
  y: number;
}
/////////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
////----------------------------------------------------------------------------------------------------------////
export interface getPoint {
  svgLayerid: string;
  x: number;
  y: number;
}
/////////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
////--------------------------------------------------------------------------------------------------////
export interface AidGraphicArg {
  dx: number;
  dy: number;
  width: number;
  height: number;
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////--------------------------------------------------------------------------------////
export class Extend {
  minX: number;
  minY: number;
  maxX: number;
  maxY: number;

  constructor(coordinateMin: Point, coordinateMax: Point) {
    this.minX = coordinateMin.x;
    this.minY = coordinateMin.y;
    this.maxX = coordinateMax.x;
    this.maxY = coordinateMax.y;
  }

  /**
   * 
   * @param subExtend  
   * @returns {boolean}
   */
  public contain(subExtend: Extend): boolean {
    if (
      this.minX <= subExtend.minX &&
      this.maxX >= subExtend.maxX &&
      this.minY <= subExtend.minY &&
      this.maxY >= subExtend.maxY
    )
      return true;
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////----------------------------------------NodeLineCore------------------------------------------////
abstract class Shape {
  id?: string;
  //
  svgLayer: SvgLayer;
  //
  mainGraphic: d3.Selection<any, any, any, any>;
  //
  aidGraphic: d3.Selection<any, any, any, any>;
  //
  textGraphic?: d3.Selection<any, any, any, any>;

  /**
   * Shape 
   * @param id id
   * @param svgLayer svg
   */
  constructor(id: string | null, svgLayer: SvgLayer) {
    this.id = id;
    this.svgLayer = svgLayer;
  }

  /**
   *  svg()
   */
  protected abstract _drawGraphic(): void;

  /**
   * svg()
   */
  protected drawGraphic(): void {
    this._drawGraphic();
    this.addEventListener();
  }

  protected abstract addEventListener(): void;

  protected abstract movePure(dx: number, dy: number, position: number): void;

  protected abstract moveToPure(point: Point, position: number): void;

  public abstract remove(): void;
}

/////////////////////////////////////////////////////////////////////////////////////////////////    enum   ////////
////-------------------------------------------------------------------------------------------////
enum RelateState {
  InnerNode4Relate,
  OutNode4Relate,
  InnerNodeRelated,
  OutNodeRelated
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////----------------------------------------------------------------------------------------------------////
export class Node extends Shape implements Point {
  //NodeIDNodeid
  static st_lastID = 0;

  /**
   * NodeID
   * @param maxNumber Node
   */
  static setCurrentMaxID(maxNumber: number) {
    Node.st_lastID = maxNumber;
  }

  x: number;
  y: number;

  /**
   * Device
   * key:deviceid
   * value:NodeDevice
   */
  devicePosMap: Map<string, number>;

  //
  relateMarkGraphic: d3.Selection<any, any, any, any>;

  //
  relateState: RelateState;

  //
  relatedDevice: Device;

  //
  relatedNode: Node;

  constructor(svgLayer: SvgLayer, point: Point, id?: string | null) {
    id == null ? (id = "ND" + Node.st_lastID) : null;
    super(id, svgLayer);

    this.x = point.x;
    this.y = point.y;

    this.devicePosMap = new Map();

    this.drawGraphic();
    this.drawTextGraphic();

    Node.st_lastID++;
  }

  /**
   * svglayer
   */
  protected _drawGraphic() {
    this.mainGraphic = (this.svgLayer.graphicLayer_mainLayer as d3.Selection<
      any,
      any,
      any,
      any
    >).append("circle");
    this.mainGraphic
      .attr("cx", this.x)
      .attr("cy", this.y)
      .attr("r", 4);
  }

  protected addEventListener(): void {
    let SM = StateManager.getInstance();

    this.mainGraphic
      .on("mousedown", () => {
        d3.event.preventDefault();
        SM.setMouseDownDeviceOrNode(this);

        if (SM.isCapturingNode() && G.mouseEventBtn() == 1) {
          SM.setNodeCaptured(this, 1);
        } else {
          //nodesvglayersvglayerelse if,
          //d3.event.nodeSelected = __this;
          this.select();
        }
      })
      .on("mouseover", () => {
        d3.event.stopImmediatePropagation();
        d3.event.preventDefault();

        //
        if (SM.isCapturingNode()) {
          //,mouseup
          SM.setNodeCaptured(this, 2);
          this.highlight();
        }
      })
      .on("mouseout", () => {
        d3.event.stopImmediatePropagation();
        d3.event.preventDefault();
        //
        if (SM.isCapturingNode()) {
          SM.setNodeCaptured(null, 2);
          this.cancelHighlight();
        }
      })
      .on("mouseup", () => {
        if (
          !SM.getType2draw() &&
          (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2) &&
          !SM.isDeviceMoved
        ) {
          this.select();
          //Node
          if (G.mouseEventBtn() == 2) {
            console.log("Node")
            ContextMenu.st_popContextMenu(
              d3.event,
              ContextMenu.st_createNodeContextMenu(this)
            );
          }
        }
      });
  }

  /**
   * 
   */
  drawTextGraphic() {
    this.textGraphic = this.svgLayer.graphicLayer_textLayer_nodeTextLayer
      .append("text")
      .attr("x", this.x)
      .attr("y", this.y)
      .attr("text-anchor", "middle")
      .attr("dy", -10)
      .text(this.id);
  }

  /**
   * svg
   */
  public showAidGraphic() {
    if (!this.aidGraphic)
      this.aidGraphic = this.svgLayer.graphicLayer_aidGraphicLayer
        .append("rect")
        .attr("transform", "translate(" + -7 + "," + -7 + ")")
        .attr("x", this.x)
        .attr("y", this.y)
        .attr("width", 14)
        .attr("height", 14)
        .attr("fill", "none")
        .attr("stroke", "red")
        .attr("stroke-width", "1");
  }

  /**
   * 
   */
  public hideAidGraphic() {
    if (this.aidGraphic) this.aidGraphic.remove();
    this.aidGraphic = null;
    StateManager.getInstance().nodeSelected = null;
  }

  /**
   * ()
   * @param dx  x
   * @param dy  y
   * @param ignoreID  id ignoreID 
   */
  public move(dx: number, dy: number, ignoreID?: string) {
    this.movePure(dx, dy);

    this.devicePosMap.forEach((pos, deviceID) => {
      if (ignoreID == deviceID) return;

      this.svgLayer.deviceMap.get(deviceID).moveNodeLinks(dx, dy, pos);
    });
  }

  /**
   * point
   * @param point
   * @param ignoreID device
   */
  public moveTo(point: Point, ignoreID?: string) {
    this.moveToPure(point);
    this.devicePosMap.forEach((pos, deviceID) => {
      if (ignoreID == deviceID) return;
      this.svgLayer.deviceMap.get(deviceID).moveToNodeLinks(point, pos);
    });
  }

  /**
   * 
   * @param dx  x
   * @param dy  y
   */
  movePure(dx: number, dy: number) {
    this.moveToPure({ x: this.x + dx, y: this.y + dy });
  }

  /**
   * 
   * @param point
   */
  public moveToPure(point: Point) {
    this.x = point.x;
    this.y = point.y;
    this.mainGraphic.attr("cx", this.x).attr("cy", this.y);
    if (this.textGraphic) this.textGraphic.attr("x", this.x).attr("y", this.y);
    if (this.aidGraphic) this.aidGraphic.attr("x", this.x).attr("y", this.y);
    if (this.relateMarkGraphic)
      this.relateMarkGraphic.attr("x", this.x - 16).attr("y", this.y);
  }

  /**
   * 
   */
  public highlight() {
    this.mainGraphic.style("fill", "#ff6a00").attr("r", 7);
  }

  /**
   * 
   */
  public cancelHighlight() {
    this.mainGraphic.style("fill", "").attr("r", 4);
  }

  /**
   * 
   * @param deviceID  id
   * @param position  nodedevice1  2
   */
  linkDevice(deviceID: string, position: number) {
    console.log("linkDevice")
    this.devicePosMap.set(deviceID, position);
  }

  /**
   * DeviceNodeline
   * @param deviceID Device
   * @param position nodeDevice
   */
  redrawAndLinkDevice(deviceID: string, position: number) {
    this.mainGraphic.remove();
    this.textGraphic.remove();
    this.drawGraphic();
    this.drawTextGraphic();
    this.linkDevice(deviceID, position);
  }

  /**
   * devicenode
   * @param id deviceid
   * @returns {boolean}  Device
   */
  public removeRecordFromDevicePosMapByID(id: string): boolean {
    this.devicePosMap.delete(id);
    if (!this.devicePosMap.size) {
      this.remove();
      return true;
    }
    return false;
  }

  /**
   * 
   */
  public remove() {
    //20170412,kid
    // if (this.linkState) {
    //     if (this.linkState.indexOf('4Link') != -1)
    //         this.removeLinkLayerContainer();
    //     else if (this.linkState.indexOf('Linked') != -1)
    //         this.removeLinkNodeAndLayerContainer();
    // }
    this.devicePosMap.clear();

    if (this.mainGraphic) this.mainGraphic.remove();
    this.mainGraphic = null;
    if (this.textGraphic) this.textGraphic.remove();
    this.textGraphic = null;
    this.hideAidGraphic();
    //this.hideLinkMarkGraphic();

    //svglayerNodeSet
    this.svgLayer.nodeSet.delete(this);
  }

  /**
   * 
   */
  select() {
    let SM = StateManager.getInstance();
    SM.clearAllAidGraphic();
    SM.nodeSelected = this; ////////////////???????
    this.showAidGraphic();
  }

  public setRelatedDevice(device: Device, isNodeInDevice: boolean = false) {
    this.relatedDevice = device;
    this.relateState = isNodeInDevice
      ? RelateState.InnerNode4Relate
      : RelateState.OutNode4Relate;
    this.showRelateMarkGraphic();
  }

  public setRelatedNode(node: Node) {
    this.relatedNode = node;
    this.relateState =
      this.relateState == RelateState.InnerNode4Relate
        ? RelateState.InnerNodeRelated
        : RelateState.OutNodeRelated;
    this.showRelateMarkGraphic();
  }

  private showRelateMarkGraphic() {
    if (this.relateMarkGraphic) this.hideRelateMarkGraphic();

    this.relateMarkGraphic = this.svgLayer.graphicLayer_textLayer_otherTextLayer
      .append("use")
      .attr("xlink:href", "#pb_svgDef_" + RelateState[this.relateState])
      .attr("x", this.x - 16)
      .attr("y", this.y);
  }

  private hideRelateMarkGraphic() {
    if (this.relateMarkGraphic) {
      this.relateMarkGraphic.remove();
      this.relateMarkGraphic = null;
    }
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////----------------------------------------------------------------------------------------------------------////
export class Line extends Shape {
  p1: Point;
  p2: Point;
  deviceID: string;

  /**
   * Line
   * @param svgLayer  svgLayer
   * @param p1  
   * @param p2  
   * @param deviceID  ID
   */
  constructor(svgLayer: SvgLayer, p1: Point, p2: Point, deviceID: string) {
    super(null, svgLayer);

    //nodepnode
    // linepnode
    //this.p1 = p1;
    //this.p2 = p2;

    //
    // let x1,x2,y1,y2;
    // x1=p1.x;
    // x2=p2.x;
    // y1=p1.y;
    // y2=p2.y;

    //
    function temp_newPoint(x: number, y: number) {
      return { x: x, y: y };
    }

    this.p1 = temp_newPoint(p1.x, p1.y);
    this.p2 = temp_newPoint(p2.x, p2.y);
    this.deviceID = deviceID;
    this.drawGraphic();
  }

  /**
   * svglayer
   */
  protected _drawGraphic() {
    this.mainGraphic = (this.svgLayer.graphicLayer_mainLayer as d3.Selection<
      any,
      any,
      any,
      any
    >).append("g");

    //Node
    this.mainGraphic
      .append("line")
      .attr("x1", this.p1.x)
      .attr("y1", this.p1.y)
      .attr("x2", this.p2.x)
      .attr("y2", this.p2.y)
      .attr("stroke", "#FFF")
      .attr("stroke-width", "5");
    this.mainGraphic
      .append("line")
      .attr("x1", this.p1.x)
      .attr("y1", this.p1.y)
      .attr("x2", this.p2.x)
      .attr("y2", this.p2.y)
      .attr("stroke", "#555")
      .attr("stroke-width", "2");
  }

  /**
   * 
   */
  protected addEventListener() {
    let sm = StateManager.getInstance();

    this.mainGraphic
      .on("mousedown", () => {
        if (
          !sm.getType2draw() &&
          (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2)
        ) {
          this.svgLayer.deviceMap.get(this.deviceID).doMouseDownLine();
        }
      })
      .on("mouseup", () => {
        //if (!G.elementType2draw && !event.button) {

        this.svgLayer.deviceMap.get(this.deviceID).doMouseUpLine();

        //}
      })
      .on("dblclick", () => {
        this.svgLayer.deviceMap.get(this.deviceID).doDoubleClick();
      });
  }

  /**
   * 
   * @param dx  x
   * @param dy  Y
   * @param position    1   
   *                                 2   
   *                               
   */
  public movePure(dx: number, dy: number, position?: number) {
    //
    function temp_movePure(
      dx: number,
      dy: number,
      point: Point,
      position: number,
      mainGraphic: d3.Selection<any, any, any, any>
    ) {
      point.x = point.x + dx;
      point.y = point.y + dy;
      mainGraphic
        .selectAll("line")
        .attr("x" + position, point.x)
        .attr("y" + position, point.y);
    }

    if (position == 1) {
      temp_movePure(dx, dy, this.p1, 1, this.mainGraphic);
    } else if (position == 2) {
      temp_movePure(dx, dy, this.p2, 2, this.mainGraphic);
    } else {
      temp_movePure(dx, dy, this.p1, 1, this.mainGraphic);
      temp_movePure(dx, dy, this.p2, 2, this.mainGraphic);
    }
  }

  /**
   * point
   * @param point
   * @param position line1p12:p2
   */
  public moveToPure(point: Point, position: number) {
    //
    // javascript!!!!!
    // !!!!!!!!!!
    // position == 1 ? this.p1 = point : this.p2 = point;
    position == 1
      ? (this.p1 = { x: point.x, y: point.y })
      : (this.p2 = { x: point.x, y: point.y });
    this.mainGraphic
      .selectAll("line")
      .attr("x" + position, point.x)
      .attr("y" + position, point.y);
  }

  /**
   * 
   */
  public remove() {
    this.p1 = null;
    this.p2 = null;
    if (this.mainGraphic) this.mainGraphic.remove();
    this.mainGraphic = null;
    this.id = null;
  }
}

