////--------------------------------------------------------------------------------------------------////
export class Core extends Shape implements Point {
  x: number;
  y: number;

  deviceID: string;
  rotate: number;
  coreType: string;
  aidGraphicArg: AidGraphicArg;
  title: string;
  textDy: number;

  /**
   *Core 
   * @param deviceID deviceid
   * @param svgLayer svgLayer
   * @param point   
   * @param rotate  
   * @param deviceType  
   * @param aidGraphicArg  
   * @param title  
   * @param textDy  
   */
  constructor(
    deviceID: string,
    svgLayer: SvgLayer,
    point: Point,
    rotate: number,
    deviceType: string,
    aidGraphicArg: AidGraphicArg = { dx: 0, dy: 0, width: 0, height: 0 },
    textDy: number = 25,
    title: string
  ) {
    super(deviceID + "_core", svgLayer);
    this.x = point.x;
    this.y = point.y;
    this.deviceID = deviceID;
    this.rotate = rotate;
    this.coreType = deviceType;
    this.aidGraphicArg = aidGraphicArg;
    this.title = title ? title : deviceID;
    this.textDy = textDy;

    this.drawGraphic();
    this.drawTextGraphic();
  }

  /**
   * svglayer
   */
  protected _drawGraphic() {
    this.mainGraphic = this.svgLayer.graphicLayer_mainLayer
      .append("use")
      .attr("xlink:href", "#pb_svgDef_" + this.coreType)
      .attr("x", this.x)
      .attr("y", this.y)
      .attr(
        "transform",
        "rotate(" + this.rotate + "," + this.x + "," + this.y + ")"
      );
  }

  /**
   * 0
   *               1
   *               2
   * @param state
   */
  public setState(state: number) {
    switch (state) {
      case 1:
        this.mainGraphic.attr(
          "xlink:href",
          "#pb_svgDef_" + this.coreType + "_disabled"
        );
        break;
      case 2:
        this.mainGraphic.attr(
          "xlink:href",
          "#pb_svgDef_" + this.coreType + "_opened"
        );
        break;
      default:
        this.mainGraphic.attr("xlink:href", "#pb_svgDef_" + this.coreType);
    }
  }

  /**
   * 
   * @param newTitle
   */
  public setTitle(newTitle: string) {
    this.title = newTitle && newTitle != "" ? newTitle : this.deviceID;
    this.textGraphic.text(this.title);
  }

  /**
   * 
   */
  protected addEventListener() {
    let sm = StateManager.getInstance();

    this.mainGraphic
      .on("mousedown", () => {
        if (
          !sm.getType2draw() &&
          (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2)
        ) {
          this.svgLayer.deviceMap.get(this.deviceID).doMouseDownCore();
        }
      })
      .on("mouseup", () => {
        if (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2)
          this.svgLayer.deviceMap.get(this.deviceID).doMouseUpCore();
      })
      .on("dblclick", () => {
        this.svgLayer.deviceMap.get(this.deviceID).doDoubleClick();
      });

    //Event.on('valuechange',(key:ATTR_ENUM,oldValue:string | number | null,newValue:string | number | null)=>{
    // });
  }

  /**
   * 
   */
  drawTextGraphic() {
    //---2017.03.30Node
    this.textGraphic = this.svgLayer.graphicLayer_textLayer_coreTextLayer
      .append("text")
      .attr("x", this.x)
      .attr("y", this.y)
      .attr("text-anchor", "middle")
      .attr("dy", this.textDy)
      .text(this.title)
      //
      .on("dblclick", () => {
        //G.openRenameDialog(__this);
      });
  }

  /**
   * 
   * @param dx  x
   * @param dy  y
   * @param width  
   * @param height  
   */
  setAidGraphicArg(dx: number, dy: number, width: number, height: number) {
    this.aidGraphicArg = { dx: dx, dy: dy, width: width, height: height };
  }

  /**
   * 
   */
  public showAidGraphic() {
    if (!this.aidGraphic)
      this.aidGraphic = this.svgLayer.graphicLayer_aidGraphicLayer
        .append("rect")
        .attr("transform", this.mainGraphic.attr("transform"))
        .attr("x", this.x + this.aidGraphicArg.dx)
        .attr("y", this.y + this.aidGraphicArg.dy)
        .attr("width", this.aidGraphicArg.width)
        .attr("height", this.aidGraphicArg.height)
        .attr("fill", "none")
        .attr("stroke", "blue")
        .attr("stroke-width", "1")
        .attr("stroke-dasharray", "4,4");
  }

  /**
   * 
   */
  public hideAidGraphic() {
    if (this.aidGraphic) this.aidGraphic.remove();
    this.aidGraphic = null;
  }

  /**
   * 
   * @param dx  x
   * @param dy  y
   */
  public move(dx: number, dy: number) {
    this.movePure(dx, dy, this.rotate);
    this.svgLayer.deviceMap.get(this.deviceID).moveCoreLinks(dx, dy);
  }

  /**
   * 
   * @param dx  x
   * @param dy  y
   * @param rotate  
   */
  public movePure(dx: number, dy: number, rotate?: number) {
    let newCoordinate = { x: this.x + dx, y: this.y + dy };
    rotate == null ? (rotate = this.rotate) : null;
    this.moveToPure(newCoordinate, rotate);
  }

  /**
   * 
   * @param newCoordinate 
   */
  public moveTo(newCoordinate: Point) {
    this.moveToPure(newCoordinate, this.rotate);
    this.svgLayer.deviceMap.get(this.deviceID).moveToCoreLinks(newCoordinate);
  }

  /**
   * 
   * @param newCoordinate  
   * @param rotate  
   */
  public moveToPure(newCoordinate: Point, rotate: number) {
    this.x = newCoordinate.x;
    this.y = newCoordinate.y;
    this.rotate = rotate;
    this.mainGraphic
      .attr("x", this.x)
      .attr("y", this.y)
      .attr(
        "transform",
        "rotate(" + this.rotate + "," + this.x + "," + this.y + ")"
      );

    this.textGraphic.attr("x", this.x).attr("y", this.y);

    if (this.aidGraphic)
      this.aidGraphic
        .attr("transform", this.mainGraphic.attr("transform"))
        .attr("x", this.x + this.aidGraphicArg.dx)
        .attr("y", this.y + this.aidGraphicArg.dy);
  }

  /**
   * 
   */
  public remove() {
    if (this.mainGraphic) this.mainGraphic.remove();
    this.mainGraphic = null;
    if (this.textGraphic) this.textGraphic.remove();
    this.textGraphic = null;
    this.hideAidGraphic();
    this.id = null;
  }
}
