import {Core, Node, Point} from "./PB_Shape";
import * as d3 from "d3-selection";
import {Project} from "./PB_Project";
import {ContainSvgLayer, Device, DEVICE_TYPE} from "./PB_Device";
import {SvgLayer} from "./PB_SvgLayer";
import {SvgLayerManager} from "./PB_SvgLayerManager";
import {DeviceCopyArgs, DeviceDrawArgs, DeviceManager} from "./PB_DeviceManager";

export class StateManager {

    static st_instance: StateManager = null;


    /**
     * 
     * @param project
     */
    static getInstance(project?: Project) {
        if (!StateManager.st_instance)
            StateManager.st_instance = new StateManager(project);
        return StateManager.st_instance;
    }


    public project: Project;

    //
    public currentSvgLayer: SvgLayer;


    //
    public lastMovingScreenCoordinate: Point = {x: 0, y: 0};
    //
    public tempLine4dragDrawing: d3.Selection<any, any, any, any>;
    // //
    // endNodeCaptured:Node;
    // //
    // startNodeCaptured:Node;
    //svgmousedown
    public isMouseDown = false;

    //svg
    public lastMovingSvgCoordinate: Point = {x: 0, y: 0};


    //
    private _isCapturingNode: boolean = false;
    public nodesCaptured: Array<Node> = [];

    public isMouseDownDeviceOrNode: boolean;
    private _mouseDownDeviceOrNode: Device | Node;

    //Node
    public nodeSelected: Node;
    public oneNodeDeviceSelected: Device;
    public oneNodeDeviceCoreSelected: Core;
    public deviceSelectedArray: Array<Device> = [];


    //Node
    public nodeMovingCaptured: Node;

    //
    public isDeviceMoved: boolean = false;

    //nodecore
    public nodeCoreMovingSet: Set<Node | Core> = new Set();

    // public isDeviceMovedById(id: string): boolean {
    //     this.nodeCoreMovingSet.forEach(device => {
    //         if (device.id == id) {
    //             return true;
    //         }
    //     });
    //     return false;
    // }

    //
    public aidSelectBox: d3.Selection<any, any, any, any>;
    public aidSelectStartCoordinate: Point;


    //
    public nodeCopyMap: Map<string, Point>;//Map<nodeID,node>;
    public deviceCopyArgsArray: Array<DeviceCopyArgs>;
    public copyStartCoordinate: Point;


    // //    -1  
    // //                 0  
    // //              1~+ 
    // public enablePasteState: number = 0;

    //
    public isCutDevices: boolean = false;

    //
    public mouseCoordinate: Point;


    private constructor(project?: Project) {
        if (!project)
            throw new Error('StateManagerproject');
        this.project = project;
    }


    /**
     * 
     */
    public clearAllAidGraphic() {
        if (this.nodeSelected) {
            this.nodeSelected.hideAidGraphic();
            this.nodeSelected = null;
        }

        if (this.oneNodeDeviceSelected) {
            this.oneNodeDeviceSelected.hideAidGraphic();
            this.oneNodeDeviceSelected = null;
        }

        if (this.oneNodeDeviceCoreSelected) {
            this.oneNodeDeviceCoreSelected.hideAidGraphic();
            this.oneNodeDeviceCoreSelected = null;
        }

        this.deviceSelectedArray.forEach(device => {
            device.hideAidGraphic();
        });
        this.deviceSelectedArray = [];
        // G.selectTab=new Array();
    }


    /**
     * 
     */
    public clearAllSelect() {
        this.clearAllAidGraphic();
        this.deviceSelectedArray = [];
    }

    /**
     * 
     */
    public mouseUpReseting() {

        //
        this.isMouseDown = false;
        //
        this._isCapturingNode = false;
        this.nodesCaptured = [];
        //
        this.isMouseDownDeviceOrNode = false;
        this._mouseDownDeviceOrNode = null;
        this.nodeMovingCaptured = null;

        //
        if (this.tempLine4dragDrawing) {
            this.tempLine4dragDrawing.remove();
            this.tempLine4dragDrawing = null;
        }

        //
        if (this.aidSelectBox) {
            this.aidSelectBox.remove();
            this.aidSelectBox = null;
        }

        this.isDeviceMoved = false;
    }

    /**
     * downNodeDevice
     * @param deviceOrNode
     */
    setMouseDownDeviceOrNode(deviceOrNode: Device | Node) {
        
        this.isMouseDownDeviceOrNode = true;
        this._mouseDownDeviceOrNode = deviceOrNode;
    }

    /**
     * downNodeDevice
     * @returns {Device|Node}
     */
    getMouseDownDeviceOrNode(): Device | Node {
        return this._mouseDownDeviceOrNode;
    }


    /**
     * getset
     * @param isStartCapture setget
     * @return {boolean} 
     */
    public isCapturingNode(isStartCapture?: boolean): boolean {
        if (typeof isStartCapture == 'boolean')
            this._isCapturingNode = isStartCapture;
        return this._isCapturingNode;

    }

    /**
     * 
     * @param node 
     * @param position Device
     */
    public setNodeCaptured(node: Node, position: number) {
        this.nodesCaptured[position] = node;
    }

    /**
     * node
     * @param position 
     */
    public getNodeCaptured(position: number): Node {
        return this.nodesCaptured[position];
    }


    /**
     * 
     * @returns {string}
     */
    public getType2draw(): DEVICE_TYPE {
        return this.project.deviceManager.ready2draw.getType2draw();
    }


    /**
     * NodeCoreSet
     */
    public createNodeCoreMovingSet() {
        this.nodeCoreMovingSet = new Set();
        this.deviceSelectedArray.forEach(device => {
            device.add2nodeCoreMovingSet();
        });
    }

    /**
     * 
     * @param newSvgLayer 
     */
    public switchCurrentSvgLayer(newSvgLayer: SvgLayer) {
        this.currentSvgLayer = newSvgLayer;
    }

    /**
     * ID
     * @param id  ID
     */
    public switchCurrentSvgLayerById(id: string) {
        this.currentSvgLayer = SvgLayerManager.getInstance().svgLayerMap.get(id);
    }

    public copyDevices() {
        this.nodeCopyMap = new Map();
        this.deviceCopyArgsArray = [];

        //
        let copyX: number = null,
            copyY: number = null;

        this.deviceSelectedArray.forEach(device => {
            device.copy(this.nodeCopyMap, this.deviceCopyArgsArray);
            let deviceMinX = device.getExtend().minX,
                deviceMinY = device.getExtend().minY;
            if (!copyX || copyX > deviceMinX)
                copyX = deviceMinX;
            if (!copyY || copyY > deviceMinY)
                copyY = deviceMinY;

        });

        this.copyStartCoordinate = {x: copyX, y: copyY};//let

        this.isCutDevices = false;
    }

    public pasteDevices(coordinate: Point = this.mouseCoordinate) {
        let dx = coordinate.x - this.copyStartCoordinate.x,
            dy = coordinate.y - this.copyStartCoordinate.y;

        this.doPasteDevices(dx, dy, this.nodeCopyMap, this.deviceCopyArgsArray, this.currentSvgLayer);

        //
        if (this.isCutDevices) {
            this.nodeCopyMap.clear();
            this.deviceCopyArgsArray = [];
        }

    }

    private doPasteDevices(dx: number, dy: number, nodeCopyMap: Map<string, Point>, deviceCopyArgsArray: Array<DeviceCopyArgs>, svgLayer: SvgLayer) {

        let newNodeMap: Map<string, Node> = new Map();

        //NodeNodeID
        nodeCopyMap.forEach((coordinate, nodeID) => {
            newNodeMap.set(nodeID, new Node(svgLayer, {x: coordinate.x + dx, y: coordinate.y + dy}));
        });
        console.log("newNodeMap")
        //
        deviceCopyArgsArray.forEach(deviceCopyArgs => {

            //
            let drawArgs: DeviceDrawArgs = new DeviceDrawArgs(deviceCopyArgs.deviceType, svgLayer);

            //
            drawArgs.setDeviceAttribute(deviceCopyArgs.attrIndex2ValueMap);

            //,
            if (typeof deviceCopyArgs.startPoint == 'string') {
                //drawArgs.startPoint = newNodeMap.get(deviceCopyArgs.startPoint);
                drawArgs.setStartPoint(newNodeMap.get(deviceCopyArgs.startPoint));
            }
            else {
                drawArgs.setStartPoint({x: deviceCopyArgs.startPoint.x + dx, y: deviceCopyArgs.startPoint.y + dy});
            }
            if (typeof deviceCopyArgs.endPoint == 'string') {
                drawArgs.setEndPoint(newNodeMap.get(deviceCopyArgs.endPoint));
            }
            else {
                drawArgs.setEndPoint({x: deviceCopyArgs.endPoint.x + dx, y: deviceCopyArgs.endPoint.y + dy});
            }

            let newDevice = DeviceManager.getInstance().newDevice(drawArgs);



            //
            if (deviceCopyArgs.subDeviceCopyArgsArray) {
                //qjsobjectContainSvgLayer
                this.doPasteDevices(0, 0, deviceCopyArgs.subNodeCopyMap, deviceCopyArgs.subDeviceCopyArgsArray, (<object>newDevice as ContainSvgLayer).innerLayer);

                //this.doPasteDevices(0, 0, deviceCopyArgs.subNodeCopyMap, deviceCopyArgs.subDeviceCopyArgsArray, (newDevice as ZeroNodeDevice).innerLayer);
                //(newDevice as ContainSvgLayer),
                // ZeroNodeDevice
            }

        });
    }

    public deleteDevices() {
        this.deviceSelectedArray.forEach(device => {
            device.hideAidGraphic();//
            device.remove();
        });

        this.deviceSelectedArray = [];
        this.oneNodeDeviceSelected = null;
    }

    public cutDevices() {
        this.copyDevices();
        this.isCutDevices = true;
        this.deleteDevices();
    }

}