/**
 * X()
 * @returns {number}
 */
export function screenX(): number {
    return d3.event.layerX;
}

/**
 * Y()
 * @returns {number}
 */
export function screenY(): number {
    return d3.event.layerY;
}

/**
 * ()
 * @returns {{x: number, y: number}} 
 */
export function screenCoordinate(): Point {
    return {x: d3.event.layerX, y: d3.event.layerY};
}


/**
 * 
 * @returns {number}   0
 *                      1
 *                      2
 *                      4
 */
export function mouseEventBtn(): number {

    let btn: number = d3.event.button;
    let s: number = d3.event.buttons;

    if (s)
        return s;
    else if (btn) {
        switch (btn) {
            case 1:
                return 4;
            default:
                return 2;
            //case 2: return 2;
        }
    }
    else
        return d3.event.type=='mouseup'?1:0; //mouseupevent

}




