export const saveprojectApi = ( issaveproaff,projectid,proaffremark,projectaffid,arclist,attrilist,elementlist,nodelist) => {
  let data = {
    issaveproaff:issaveproaff,
    projectid:projectid,
    remark:proaffremark,
    projectaffid:projectaffid,
    arclist:arclist,
    attrilist:attrilist,
    elementlist:elementlist,
    nodelist:nodelist
  }
  console.log(data)
  return axios.request({
    url: '/gwwlmxgl/saveproject',
    data,
    method: 'post'
  })
}
