<template>
  <el-dialog title="" :visible.sync="dialogVisible" width="20%">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      size="mini"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="gkname">
        <el-input v-model="ruleForm.gkname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="gkms">
        <el-input v-model="ruleForm.gkms" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm');"></el-button>
        <el-button @click="dialogVisible = false;"> </el-button>
      </el-form-item>
    </el-form>
    <!-- <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="dialogVisible = false"> </el-button>
    </span>-->                                       
  </el-dialog>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
        gkname: "",
        gkms: ""
      },
      rules: {
        gkname: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 12, message: " 2  12 ", trigger: "blur" }
        ]
      },
      dialogVisible: false,
    };
  },
  props: {
    //Project
    project: null
  },
  methods: {
    opengkdialog() {
      this.dialogVisible = true;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.project.isArcExist(this.ruleForm.gkname)) {
          let projectArc=   this.project.newArc(this.ruleForm.gkname, this.ruleForm.gkms);
          this.$emit("treeAddArc", projectArc);
          this.dialogVisible = false;
          } else {
            this.$message.error("!");
          }
        }
      });
    }
  }
};
</script>


