
<template>
 <div> 
  <el-menu
    :default-active="activeIndex"
    class="el-menu-demo"
    mode="horizontal"
    @select="handleSelect"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
    <el-submenu index="1">
      <template slot="title"></template>
      <el-menu-item index="newproject"></el-menu-item>
      <el-menu-item index="openproject"></el-menu-item>
      <el-menu-item index="saveproject"></el-menu-item>
      <el-menu-item index="projectmanage"></el-menu-item>
    </el-submenu>
      <el-submenu index="2">
      <template slot="title"></template>
      <el-menu-item index="topoanalyze"></el-menu-item>
      <el-menu-item index="moxingjiancha"></el-menu-item>
      <el-menu-item index="1-3"></el-menu-item>
    </el-submenu>
    <el-submenu index="3">
      <template slot="title"></template>
      <el-menu-item index="simulatemanage"></el-menu-item>
      <el-menu-item index="simulateset"></el-menu-item>
    </el-submenu>
    <el-submenu index="4">
      <template slot="title"></template>
      <el-menu-item index="1-1"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
    </el-submenu>
  <el-submenu index="5">
      <template slot="title">/</template>
      <el-menu-item index="1-1"></el-menu-item>      
      <el-menu-item index="1-2"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
    </el-submenu>
  </el-menu>
    <!--  -->
    <el-dialog
    title=""
    :visible.sync="newprojectopendialog"
    width="30%"
   >
   <new-project></new-project>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="openprojectopendialog"
      width="50%"
      >
      <open-project :projectlist="projectlist" @openprojectfun="openprojectfun" :loading="openprojectloading"></open-project>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="projectmanageropendialog"
      width="50%"
      >
      <project-manager :projectlist="projectlist" @deleteproject="deleteproject" @openprojectaff="openprojectaff" :loading="openprojectloading"></project-manager>
    </el-dialog>
     <!--  -->
    <el-dialog
      title=""
      :visible.sync="projectaffmanageropendialog"
      width="50%"
      >
      <projectaff-manager :projectafflist="projectafflist" @deleteprojectaff="deleteprojectaff" @projectaffidbyopenproject="projectaffidbyopenproject" :loading="openprojectaffloading"></projectaff-manager>
    </el-dialog>
     <!--  -->
    <el-dialog
      title=""
      :visible.sync="simulatemanageopendialog"
      width="50%"
      >
      <simulate-manage :simulatemanagelist="simulatemanagelist" @getsimulatemanagelist="getsimulatemanagelist" :loading="opensimulatemanageloading"></simulate-manage>
    </el-dialog>
     <!--  -->
    <el-dialog
      title=""
      :visible.sync="simulatesetopendialog"
      width="60%"
      >
      <simulate-set ></simulate-set>
    </el-dialog>
  </div> 
</template>
<script>
import NewProject from "../munedialog/wulimoxingguanli/newproject";
import OpenProject from "../munedialog/wulimoxingguanli/openproject";
import ProjectManager from "../munedialog/wulimoxingguanli/projectmanager";
import ProjectaffManager from "../munedialog/wulimoxingguanli/projectaffmanager";
import SimulateManage from "../munedialog/fangzhenmoni/simulatemanage";
import SimulateSet from "../munedialog/fangzhenmoni/simulateset";
import {
  getProjectListApi,
  proaffcountbyprojectidApi,
  getproaffbyprojectidlistApi
} from '@/api/gwwlmxgl'
import {
  getsimulationlistApi
} from '@/api/fzmnfa'
export default {
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      /// 
      newprojectopendialog:false,
      //Model
      newprojectmodel:[],
      ///
      openprojectopendialog:false,
      ///
      projectmanageropendialog:false,
      ///
      projectaffmanageropendialog:false,
      ///
      simulatemanageopendialog:false,
      //
      simulatesetopendialog:false,
      //
      projectlist:[],
      openprojectloading:false,
      //
      projectafflist:[],
      openprojectaffloading:false,
      //
      simulatemanagelist:[],
      opensimulatemanageloading:false
    };
  },
  props:{
    project:{
      default:Array
    }
  },
  components:{
    NewProject,
    OpenProject,
    ProjectManager,
    ProjectaffManager,
    SimulateManage,
    SimulateSet
  },
  methods: {
    handleSelect(key) {
      switch (key) {
        case "newproject":
          this.newprojectopendialog=true;
          break;
        case "openproject":
          this.openprojectopendialog=true
          this.openprojectloading=true;
          this.getprojectlist();
          break;
        case "saveproject":
          this.saveprojectfun();
        break;
        case "projectmanage":
          this.projectmanageropendialog=true
          this.openprojectloading=true;
          this.getprojectlist();
        break;
        case "simulatemanage":
          this.simulatemanageopendialog=true;
          this.getsimulatemanagelist(2)
          break;
        case "simulateset":
          this.simulatesetopendialog=true;
          break;
        default:
          break;
      }
    },
    //
    getsimulatemanagelist(idtype)
    {
      let projectid=1;
      if(idtype===1)
      {
        projectid=this.project.id
      }
      else if(idtype===2)
      {
        projectid=this.project.affid
      }
      alert(idtype)
      this.opensimulatemanageloading=true;
      getsimulationlistApi(projectid,idtype).then(res => {
             this.opensimulatemanageloading=false;
             if(res.data.status==200)
             {
               this.simulatemanagelist=res.data.data
             }
             else
             {
                this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
              });
             }
             
           }).catch(err => {
             this.opensimulatemanageloading=false;
            this.$message({
              message: ':'+err,
              type: 'error',
              duration:8000
            });
          })
    },
    //
    openprojectaff(projectaff){
      this.projectmanageropendialog=false;
      this.openprojectaffloading=true;
      this.projectaffmanageropendialog=true;
      getproaffbyprojectidlistApi(projectaff.projectidPro).then(res=>{
         this.openprojectaffloading=false
        if(res.data.status===200)
        {
            let projectaffArre=[]
            res.data.data.forEach(element => {
              projectaffArre.push({
                projectidProaff:element.projectidProaff,
                createdateProaff:element.createdateProaff,
                prousernameProaff:element.prousernameProaff,
                remarkProaff:element.remarkProaff,
                projectnamePro:projectaff.projectnamePro,
              })
            });
            console.log(projectaffArre)
            this.projectafflist=projectaffArre
        }
        else
        {
           this.$message({
              message: res.data.msg,
              type: 'error',
              duration:8000
            });
        }
        
      }).catch(err => {
         this.openprojectaffloading=false
            this.$message({
              message: ':'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //
    deleteprojectaff(projectidproaff)
    {
      if(projectidproaff==this.project.newid)
        {
          this.$message({
            message: '',
            type: 'warning'
          });
          return false;
        }
       this.$confirm("", '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {
              this.deleteprojectfun(projectidproaff)  
            }).catch((action) => {
              //if(action === 'cancel')
              //this.projectopenfun(projectid)   
        });
       
    },
    //
    deleteprojectafffun(projectidproaff)
    {
       deleteprojectaffbyprojectaffidApi(projectidproaff).then(res=>{
        if(res.data.status===200)
        {
          this.openprojectaff(this.project.id);
        }
        else
        {
           this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
            });
        }
      }).catch(err => {
            this.$message({
              message: ':'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //
    deleteproject(projectid)
    {
      alert(projectid)
      alert(this.project.id)
        if(projectid==this.project.id)
        {
          this.$message({
            message: '',
            type: 'warning'
          });
          return false;
        }
        proaffcountbyprojectidApi(projectid).then(res=>{
              this.$confirm(''+res.data.data+"", '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {
              this.deleteprojectfun(projectid)  
            }).catch((action) => {
              //if(action === 'cancel')
              //this.projectopenfun(projectid)   
            });
        }).catch(err => {
            this.$message({
              message: ':'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //
    deleteprojectfun(projectid)
    {
      deleteprojectbyprojectidApi(projectid).then(res=>{
        if(res.data.status===200)
        {
          this.getProjectListApi();
        }
        else
        {
           this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
              });
        }
      }).catch(err => {
            this.$message({
              message: ':'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //
    getprojectlist()
    {
      getProjectListApi().then(res => {
             this.openprojectloading=false;
             if(res.data.status==200)
             {
               this.projectlist=res.data.data
             }
             else
             {
                this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
              });
             }
             
           }).catch(err => {
             this.openprojectloading=false;
            this.$message({
              message: ':'+err,
              type: 'error',
              duration:8000
            });
          })
    },
    //
    saveprojectfun()
    {
        this.$emit("saveprojectmsg")
    },
    //ID
    projectaffidbyopenproject(projectid)
    {
      this.projectaffmanageropendialog=false;
      this.$emit("projectopening",projectid,2)
    },
    //ID
    openprojectfun(projectid)
    {
      this.openprojectopendialog=false
      this.$emit("projectopening",projectid,1)
    },
    //
    closedialog(dialogname)
    {
      //alert(dialogname)
       switch (dialogname) {
        case "newproject":
          this.newprojectopendialog=false;
          break;
        case "openproject":
          this.openprojectopendialog=false;
          break;
        case "simulateset":
          this.simulatesetopendialog=false;
        break;
        default:

          break;
      }
    }
  }
};
</script>