import { Node, Point ,getPoint} from "./PB_Shape";
import { SvgLayer } from "./PB_SvgLayer";
import {
  Device,
  DEVICE_TYPE,
  DeviceArg,
  DEVICES,
  OneNodeDevice,
  TwoNodeDevice,
  ZeroNodeDevice
} from "./PB_Device";
import { ArcManager } from "./PB_ArcManager";
import { Attribute } from "./PB_Attribute";
import AIV_Map = Attribute.AttrIndex2ValueMap;
import ATTR_ENUM = Attribute.ATTR_ENUM;
import { StateManager } from "./PB_StateManager";
import { SvgLayerManager } from "./PB_SvgLayerManager";

export interface DeviceCopyArgs {
  startPoint: Point | string; //| nodeID
  endPoint: Point | string; //| nodeID
  deviceType: DEVICE_TYPE;
  attrIndex2ValueMap?: AIV_Map;
  subNodeCopyMap?: Map<string, Point>; //Map<nodeID,node>
  subDeviceCopyArgsArray?: Array<DeviceCopyArgs>;
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////--------------------------------------------------------------------------------------////
export class DeviceDrawArgs {
  private startPoint: Point | Node; // | node
  private endPoint: Point | Node; // | node
  private deviceType: DEVICE_TYPE;
  private svgLayer: SvgLayer;
  private id: string;
  private attrIndex2ValueMap: AIV_Map; //

  constructor(
    deviceType?: DEVICE_TYPE,
    svgLayer?: SvgLayer,
    startPoint?: Point | Node,
    endPoint?: Point | Node,
    id?: string,
    attrIndex2ValueMap?: AIV_Map
  ) {
    this.deviceType = deviceType;
    this.svgLayer = svgLayer;
    this.startPoint = startPoint;
    this.endPoint = endPoint;
    this.id = id;
    this.attrIndex2ValueMap = attrIndex2ValueMap;
  }

  getDeviceId(): string {
    return this.id;
  }

  setDeviceId(id: string) {
    this.id = id;
  }

  /**
   * 
   * @param type DEVICES
   */
  setType2draw(type: DEVICE_TYPE) {
    this.deviceType = type;
  }

  getType2draw(): DEVICE_TYPE {
    return this.deviceType;
  }

  setStartPoint(point: Point | Node) {
    this.startPoint = point;
  }

  getStartPoint(): Point | Node {
    return this.startPoint;
  }

  setEndPoint(point: Point | Node) {
    this.endPoint = point;
  }

  getEndPoint(): Point | Node {
    return this.endPoint;
  }

  setCurrentSvgLayer(svgLayer: SvgLayer) {
    this.svgLayer = svgLayer;
  }

  getCurrentSvgLayer(): SvgLayer {
    return this.svgLayer;
  }

  setDeviceAttribute(attrIndex2ValueMap: AIV_Map) {
    if (!this.attrIndex2ValueMap) {
      this.attrIndex2ValueMap = new Map<number, string | number | null>();
    }
    this.attrIndex2ValueMap = attrIndex2ValueMap;
  }

  getDeviceAttribute(): AIV_Map {
    return this.attrIndex2ValueMap;
  }

  /**
   * 
   * @returns {number} 
   */
  computeDistance(): number {
    return (
      Math.pow(this.endPoint.x - this.startPoint.x, 2) +
      Math.pow(this.endPoint.y - this.startPoint.y, 2)
    );
  }

  /**
   * 
   * @returns {boolean}
   */
  isReady(): boolean {
    return !!(
      this.deviceType &&
      this.startPoint &&
      this.endPoint &&
      this.svgLayer
    );
  }

  /**
   * x,y
   * @param threshold   ,1000
   * @param newDx  x  100
   */
  adjustCoordinate(threshold: number = 1000, newDx: number = 100) {
    if (this.computeDistance() < threshold) {
      this.endPoint.x = this.startPoint.x + newDx;
      this.endPoint.y = this.startPoint.y;
    }
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////--------------------------------------------------------------------------------------------////
export class DeviceManager {
  static st_instance: DeviceManager = null;

  /**
   * 
   * @returns {DeviceManager}
   */
  static getInstance() {
    if (!DeviceManager.st_instance)
      DeviceManager.st_instance = new DeviceManager();
    return DeviceManager.st_instance;
  }

  /**
   * new
   */
  private constructor() {
    this.ready2draw = new DeviceDrawArgs();
  }

  //
  ready2draw: DeviceDrawArgs;

  private _deviceMap: Map<string, Device> = new Map();

  getDeviceByID(deviceID: string): Device {
    return this._deviceMap.get(deviceID);
  }

  removeDeviceByID(deviceID: string): void {
    if (this._deviceMap.has(deviceID)) {
      this._deviceMap.delete(deviceID);
    }
  }
   
  getDeviceMap(): Map<string, Device> {
    return this._deviceMap;
  }

  hasDevice(): boolean {
    return !!this._deviceMap.size;
  }

  /**
   * 
   * @returns {Device} 
   */
  drawReadyDevice(): Device {
    //ZeroNode
    //this.ready2draw.adjustCoordinate();
    console.log(this.ready2draw);
    let newDevice = this.newDevice(this.ready2draw);
    this.reNewReady2draw();
    return newDevice;
  }

  /**
   * 
   */
  reNewReady2draw() {
    this.ready2draw = new DeviceDrawArgs();
  }

  /**
   * svg
   * @param arg 
   * @returns {Device}  
   */
  newDevice(arg: DeviceDrawArgs): Device | null {
    let device: Device;
    let deviceArg: DeviceArg = DEVICES.get(arg.getType2draw());
    console.log(arg.getType2draw());
    switch (deviceArg.classType) {
      case 0:
        device = new ZeroNodeDevice(
          arg.getType2draw(),
          arg.getCurrentSvgLayer(),
          arg.getStartPoint(),
          arg.getEndPoint(),
          arg.getDeviceId(),
          arg.getDeviceAttribute()
        );
        break;
      case 1:
        //ready2draw
        if (this.ready2draw.getStartPoint()) this.ready2draw.adjustCoordinate();

        device = new OneNodeDevice(
          arg.getType2draw(),
          arg.getCurrentSvgLayer(),
          arg.getStartPoint(),
          arg.getEndPoint(),
          arg.getDeviceId(),
          arg.getDeviceAttribute()
        );
        break;
      case 2:
        //ready2draw
        if (this.ready2draw.getStartPoint()) this.ready2draw.adjustCoordinate();

        device = new TwoNodeDevice(
          arg.getType2draw(),
          arg.getCurrentSvgLayer(),
          arg.getStartPoint(),
          arg.getEndPoint(),
          arg.getDeviceId(),
          arg.getDeviceAttribute()
        );
        break;
    }

    //_deviceMap
    this._deviceMap.set(device.id, device);
    //
    ArcManager.getInstance().addNewDeviceToAllArc(device);
    return device;
  }

  loadDevice(arg: DeviceDrawArgs): Device | null {
    let device: Device;
    let deviceArg: DeviceArg = DEVICES.get(arg.getType2draw());
    switch (deviceArg.classType) {
      case 0:
        device = new ZeroNodeDevice(
          arg.getType2draw(),
          arg.getCurrentSvgLayer(),
          arg.getStartPoint(),
          arg.getEndPoint(),
          arg.getDeviceId(),
          arg.getDeviceAttribute()
        );
        break;
      case 1:
        //ready2draw
        if (this.ready2draw.getStartPoint()) this.ready2draw.adjustCoordinate();

        device = new OneNodeDevice(
          arg.getType2draw(),
          arg.getCurrentSvgLayer(),
          arg.getStartPoint(),
          arg.getEndPoint(),
          arg.getDeviceId(),
          arg.getDeviceAttribute()
        );
        break;
      case 2:
        //ready2draw
        if (this.ready2draw.getStartPoint()) this.ready2draw.adjustCoordinate();

        device = new TwoNodeDevice(
          arg.getType2draw(),
          arg.getCurrentSvgLayer(),
          arg.getStartPoint(),
          arg.getEndPoint(),
          arg.getDeviceId(),
          arg.getDeviceAttribute()
        );
        break;
    }

    //_deviceMap
    this._deviceMap.set(device.id, device);
    return device;
  }

  /**
   * 
   * @param point  
   * @param position 12
   */
  public setReady2drawPoint(point: Point, position: number) {
    //position == 1 ? this.ready2draw.startPoint = point : this.ready2draw.endPoint = point;
    position == 1
      ? this.ready2draw.setStartPoint(point)
      : this.ready2draw.setEndPoint(point);
  }

  /**
   * 
   * @param position 12
   * @returns {Point} 
   */
  public getReady2drawPoint(position: number): Point {
    return position == 1
      ? this.ready2draw.getStartPoint()
      : this.ready2draw.getEndPoint();
  }

  public getDeviceJsonArr(): [any] {
    let arr: any = [];

    this._deviceMap.forEach(device => {
      arr.push(device.getJsonObject());
    });

    return arr;
  }

  public getNodeJsonArr(): [any] {
    let arr: any = [];
    let nodeId2PtMap: Map<string, getPoint> = new Map();
     console.log("")
    this._deviceMap.forEach(device => {
      if (1 == device.getNodeNum()) {
        let nodeId: string = (device as OneNodeDevice).getNode().id;
        if (!nodeId2PtMap.has(nodeId))
          nodeId2PtMap.set(nodeId, {
            svgLayerid:device.svgLayer.id,
            x: (device as OneNodeDevice).getNode().x,
            y: (device as OneNodeDevice).getNode().y
          });
      } else if (2 == device.getNodeNum()) {
        for (let i = 1; i <= 2; i++) {
          let nodeId: string = device.nodes[i].id;
          if (!nodeId2PtMap.has(nodeId))
            nodeId2PtMap.set(nodeId, {
              svgLayerid:device.svgLayer.id,
              x: device.nodes[i].x,
              y: device.nodes[i].y
            });
        }
      }
    });

    nodeId2PtMap.forEach((pt, nodeId) => {
      arr.push({
        id: nodeId,
        pt: pt
      });
    });

    return arr;
  }

  public setDeviceJsonArr(nodeArray: [any], deviceArray: [any]): void {
    //let nodeId2PtMap: Map<string, Point> = new Map();
    let newNodeMap: Map<string, Node> = new Map();
    for (let device of deviceArray) {
      if(device.p1 == null && device.p2 == null)
      {
      let arg: DeviceDrawArgs = new DeviceDrawArgs();
      arg.setDeviceId(device.id);
      arg.setType2draw(device.type);
      let svglayers:SvgLayer=SvgLayerManager.getInstance().loadSvgLayer(device.layer)
      arg.setCurrentSvgLayer(svglayers);

      arg.setStartPoint(device.cp);
      arg.setEndPoint(device.cp);
      arg.setDeviceAttribute(null);
      this._deviceMap.set(device.id, this.loadDevice(arg));
      }
    }
    for (let node of nodeArray) {
      let svglayers:SvgLayer=SvgLayerManager.getInstance().loadSvgLayer(node.layerid)
      newNodeMap.set(node.id, new Node(svglayers, { x: node.pt.x, y: node.pt.y }));
    }

    for (let device of deviceArray) {
      if(device.p1 == null && device.p2 == null)
      {
        continue;
      }
      let arg: DeviceDrawArgs = new DeviceDrawArgs();
      arg.setDeviceId(device.id);
      arg.setType2draw(device.type);
      let svglayers:SvgLayer=SvgLayerManager.getInstance().loadSvgLayer(device.layer)
      arg.setCurrentSvgLayer(svglayers);

      if (device.p1 != null && device.p2 == null) {
        arg.setStartPoint(newNodeMap.get(device.p1));
        arg.setEndPoint(device.cp);
      } else if (device.p1 == null && device.p2 != null) {
        arg.setStartPoint(device.cp);
        arg.setEndPoint(newNodeMap.get(device.p2));
      } else if (device.p1 != null && device.p2 != null) {
        //x: node.pt.x, y: node.pt.y 
        //arg.setStartPoint(nodeId2PtMap.get(device.p1));
        //arg.setEndPoint(nodeId2PtMap.get(device.p2));
        arg.setStartPoint(newNodeMap.get(device.p1));
        arg.setEndPoint(newNodeMap.get(device.p2));
      } else {
        arg.setStartPoint(device.cp);
        arg.setEndPoint(device.cp);
      }
      //
      arg.setDeviceAttribute(null);
      this._deviceMap.set(device.id, this.loadDevice(arg));
      // this.reNewReady2draw();
    }
  }

  public removeAll(): void {
    this._deviceMap.clear();
  }
}
