import { Project } from "./PB_Project";
import { SvgLayer } from "./PB_SvgLayer";
import { Point } from "./PB_Shape";
import { StateManager } from "./PB_StateManager";
import { DEVICE_TYPE } from "./PB_Device";

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////----------------------------------------svg-------------------------------------------------////
export class SvgLayerManager {
  static st_instance: SvgLayerManager;

  /**
   * 
   * @param project  
   * @returns {SvgLayerManager}
   */
  static getInstance(project?: Project): SvgLayerManager {
    console.log(project)
    if (project) {
      if (!SvgLayerManager.st_instance) {
        SvgLayerManager.st_instance = new SvgLayerManager(project);
      } else {
        throw new Error("SvgLayerManagerproject");
      }
    } else {
      if (!SvgLayerManager.st_instance) {
        throw new Error(
          "SvgLayerManagerproject"
        );
      }
    }
    return SvgLayerManager.st_instance;
  }
  
  //Map
  svgLayerMap: Map<string, SvgLayer>;

  //
  project: Project;

  //
  // /**
  //  * 
  //  */
  // public reseting(){
  //     this._isCapturingNode=false;
  //     this.nodesCaptured=[];
  //     this.isMouseDownDeviceOrNode=false;
  //     this._mouseDownDeviceOrNode=null;
  //     this.nodeMovingCaptured=null;
  //     this.svgLayerMap.forEach()
  // }
  
  /**
   * new
   * @param project 
   */
  private constructor(project: Project) {
    this.svgLayerMap = new Map();
    this.project = project;
    this.createDefaultSvgLayer();
  }
  public removeAll(): void 
  {
    //SvgLayerManager.st_instance=null;
    this.svgLayerMap.clear()
  }
  /**
   * 
   * @param parentDevice  
   * @returns {SvgLayer} svglayer
   */
  newSvgLayer(parentDevice?: any): SvgLayer {
    let newLayer = new SvgLayer(this, parentDevice);
    this.svgLayerMap.set(newLayer.id, newLayer);
    return newLayer;
  }
  
  loadSvgLayer(id,parentDevice?: any): SvgLayer {
    let svglayer = this.svgLayerMap.get(id);
    if(svglayer)
    {
      return svglayer;
    }
    else
    {
      let newLayer = new SvgLayer(this, parentDevice);
      this.svgLayerMap.set(newLayer.id, newLayer);
      return newLayer;
    }
   
  }
  /**
   * 
   */
  createDefaultSvgLayer() {
    StateManager.getInstance().currentSvgLayer = this.newSvgLayer();
  }

  /**
   * 
   * @returns {SvgLayer}
   */
  getDefaultSvgLayer(): SvgLayer {
    let svglayer = this.svgLayerMap.get("defaultLayer");
    if (!svglayer) {
      throw new Error("defaultLayer");
    }
    return svglayer;
  }

  /**
   * 
   * @returns {string} 
   */
  public getType2draw(): DEVICE_TYPE {
    return this.project.getType2draw();
  }

  /**
   * 
   * @param point  
   * @param position 12
   */
  public setReady2drawPoint(point: Point, position: number) {
    this.project.setReady2drawPoint(point, position);
  }

  /**
   * 
   * @param position 12
   * @returns {Point} 
   */
  public getReady2drawPoint(position: number): Point {
    return this.project.getReady2drawPoint(position);
  }

}
