<template>
    <el-form ref="newprojectform" :model="newprojectmodel" :rules="rules" label-width="90px">
      <el-form-item label=":" prop="projectnamePro">
        <el-input v-model="newprojectmodel.projectnamePro" placeholder=""></el-input>
      </el-form-item>
    <el-form-item label=":">
     <el-input type="textarea" v-model="newprojectmodel.remarkPro"  placeholder=""></el-input>
    </el-form-item>
     <el-form-item>
      <el-button type="primary" @click="onSubmit"></el-button>
      <el-button @click="resetForm"></el-button>
    </el-form-item>
    </el-form>  
</template>
<script>
import {
  newProjectApi,
} from '@/api/gwwlmxgl'
export default {
  data() {
    return {
      //Model
      newprojectmodel:{
        projectnamePro:"",
        remarkPro:""
      },
      //
       rules: {
          projectnamePro: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 10, message: ' 3  5 ', trigger: 'blur' }
          ],
       }
    };
  },
  methods:{
    onSubmit()
    {
      let _this=this;
      console.log(_this)
      this.$refs["newprojectform"].validate((valid) => {
          if (!valid) {
            return false
          }
          newProjectApi(this.newprojectmodel).then(res => {
            console.log(res.data)
            //if()
            if (res.data.status===200)
            {
              //xSVG
              _this.$parent.$parent.$parent.$parent.$parent.projectload(res.data.data.projectid,_this.newprojectmodel.projectnamePro,res.data.data.projectaffid);
              _this.$parent.$parent.closedialog("newproject");
              this.resetForm()
              this.$message({
                message: '',
                type: 'success',
                duration:8000
              });
            }
            else
            {
              this.$message({
                message: ':'+res.data.msg,
                type: 'error',
                duration:8000
              });
            }
          }).catch(err => {
            this.$message({
              message: ':'+err,
              type: 'error',
              duration:8000
            });
          })
      })
    },
    resetForm() {
        this.$refs["newprojectform"].resetFields();
      }
  }
}
</script>