import { AidGraphicArg, Core, Extend, Line, Node, Point } from "./PB_Shape";
import { SvgLayer } from "./PB_SvgLayer";
import { StateManager } from "./PB_StateManager";
import * as G from "./PB_GlobeVar";
import { SvgLayerManager } from "./PB_SvgLayerManager";
import { DeviceCopyArgs, DeviceManager } from "./PB_DeviceManager";
import * as d3 from "d3-selection";
import { ContextMenu } from "./PB_ContextMenu";
import { ArcManager } from "./PB_ArcManager";
import { copyMap } from "./PB_Tools";
import { Attribute } from "./PB_Attribute";
import AttrIndex2ValueMap = Attribute.AttrIndex2ValueMap;
import ATTR_ENUM = Attribute.ATTR_ENUM;
import Attrs = Attribute.Attrs;
import DEVICE_CLASSIFY_ENUM = Attribute.DEVICE_CLASSIFY_ENUM;

export interface ContainSvgLayer {
  //
  innerLayer: SvgLayer;
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////---------------------------------------- Device-------------------------------------------------////
export abstract class Device {
  //static st_CLASS_NAME = 'Device';

  //
  static st_PopAttributeDlg: Function;

  id: string;
  //
  deviceType: DEVICE_TYPE;
  svgLayer: SvgLayer;
  nodes: Node[];
  line: Line;
  core: Core;
  //
  attrIndex2ValueMap: Attribute.AttrIndex2ValueMap;

  /**
   * Device
   * @param id  id
   * @param deviceType
   * @param svgLayer  svg
   * @param nodes  
   * @param line  
   * @param core  
   * @param attrIndex2ValueMap  ->Map
   */
  constructor(
    id: string | null,
    deviceType: DEVICE_TYPE,
    svgLayer: SvgLayer,
    nodes: Node[],
    line: Line,
    core: Core,
    attrIndex2ValueMap: AttrIndex2ValueMap
  ) {
    this.id = id;
    this.deviceType = deviceType;
    this.svgLayer = svgLayer;
    this.nodes = nodes;
    this.line = line;
    this.core = core;
    if (attrIndex2ValueMap == null) {
      attrIndex2ValueMap = Attribute.getDefault_AIV_Map(deviceType);
      attrIndex2ValueMap.set(0, this.id)
    } else {
      attrIndex2ValueMap.set(0, this.id)
      attrIndex2ValueMap = copyMap(attrIndex2ValueMap)
    };

    this.attrIndex2ValueMap = attrIndex2ValueMap;
    this.svgLayer.addInnerDevice2DeviceMap(id, this);

    this.refreshAllGraphic();
    DEVICES.get(this.deviceType).lastID++;
  }

  /**
   * x
   * @param p1 
   * @param p2 
   * @returns {number} 
   */
  static computeRotate(p1: Point, p2: Point): number {
    let result = (Math.atan2(p2.y - p1.y, p2.x - p1.x) / Math.PI) * 180.0;
    return result == Infinity || (typeof result === "number" && isNaN(result))
      ? 0
      : result;
  }

  /**
   * 
   * @param p1
   * @param p2
   * @returns {{x: number, y: number}}                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  
   */
  static computeCenterPosition(p1: Point, p2: Point): Point {
    return { x: (p1.x + p2.x) / 2, y: (p1.y + p2.y) / 2 };
  }

  /**
   * point
   * @param p1
   * @param p2
   * @returns {{minX: number, minY: number, maxX: number, maxY: number}}
   */
  static st_getExtend(p1: Point, p2: Point): Extend {
    let extend = new Extend(p1, p1);

    if (extend.minX > p2.x) extend.minX = p2.x;
    if (extend.maxX < p2.x) extend.maxX = p2.x;
    if (extend.minY > p2.y) extend.minY = p2.y;
    if (extend.maxY < p2.y) extend.maxY = p2.y;

    return extend;
  }

  /**
   * title
   * @returns {string}
   */
  public getTitle(): string {
    return this.attrIndex2ValueMap.get(0).toString();
  }
   
  /**
   * Node
   * @param node Node
   * @param pos Device
   */
  public replaceNode(node: Node, pos: number) {
    this.nodes[pos].removeRecordFromDevicePosMapByID(this.id);
    this.nodes[pos] = node;
    node.redrawAndLinkDevice(this.id, pos);
  }

  /**
   * Node
   * @param pos Node
   */
  public replaceNodeWithNewOne(pos: number) {
    let newNode = new Node(this.svgLayer, this.nodes[pos]);
    newNode.linkDevice(this.id, pos);

    this.nodes[pos].removeRecordFromDevicePosMapByID(this.id);
    this.nodes[pos] = newNode;
    this.nodes[pos].move(0, 20);
  }

  /**
   * 
   */
  public showAidGraphic() {
    this.nodes.forEach(node => {
      if (node) node.showAidGraphic();
    });
    if (this.core) this.core.showAidGraphic();
  }

  /**
   *
   */
  public hideAidGraphic() {
    this.nodes.forEach(node => {
      if (node) node.hideAidGraphic();
    });
    if (this.core) this.core.hideAidGraphic();
  }

  /**
   *Downcore
   */
  public doMouseDownCore(): void {
    let sm = StateManager.getInstance();
    sm.setMouseDownDeviceOrNode(this);

    //,
    let flag_isSelectDevice = true;
    //
    sm.deviceSelectedArray.forEach(device => {
      if (device == this) flag_isSelectDevice = false;
    });
    //
    if (flag_isSelectDevice) {
      sm.clearAllSelect();
      this.select();
    }
  }

  /**
   *Upcore
   */
  public doMouseUpCore() {
    let sm = StateManager.getInstance();
    if (!sm.getType2draw() && !sm.isDeviceMoved) {
      //
      if (G.mouseEventBtn() == 1) {
        sm.clearAllSelect();
        this.select();
      }
      //
      else if (G.mouseEventBtn() == 2)
      {
      console.log("")
        ContextMenu.st_popContextMenu(
          d3.event,
          ContextMenu.st_createDeviceContextMenu()
        );
      }
    }
  }

  /**
   *Downline
   */
  public doMouseDownLine() {
    this.doMouseDownCore();
  }

  /**
   *Upline
   */
  public doMouseUpLine() {
    this.doMouseUpCore();
  }

  /**
   * 
   */
  public doDoubleClick() {
    console.log(this)
    Device.st_PopAttributeDlg(this);
  }

  /**
   * 
   */
  public select() {
    this.showAidGraphic();
    StateManager.getInstance().deviceSelectedArray.push(this);
  }

  /**
   * Node
   * @param dx  x
   * @param dy  y
   * @param position  Node
   */
  abstract moveNodeLinks(dx: number, dy: number, position: number): void;

  /**
   * NodeNode
   * @param newCoordinate 
   * @param position  Node
   */
  abstract moveToNodeLinks(newCoordinate: Point, position: number): void;

  /**
   * Core
   * @param dx  x
   * @param dy  y
   */
  abstract moveCoreLinks(dx: number, dy: number): void;

  /**
   * CoreCore
   * @param newCoordinate 
   */
  abstract moveToCoreLinks(newCoordinate: Point): void;

  /**
   * nodecoreSet
   */
  abstract add2nodeCoreMovingSet(): void;

  /**
   * Node
   */
  abstract getNodeNum(): number;

  /**
   * 
   */
  abstract getExtend(): Extend;

  /**
   * 
   */
  abstract getClassName(): string;

  /**
   * 
   */
  abstract copy(
    nodeCopyMap: Map<string, Point>,
    deviceCopyArray: Array<DeviceCopyArgs>
  ): void;

  /**
   * 
   */
  protected abstract _remove(): void; //Undo/redo

  abstract getJsonObject(): {};

  /**
   * 
   */
  public remove(): void {
    this._remove();
    //
    DeviceManager.getInstance().removeDeviceByID(this.id);
    //
    ArcManager.getInstance().removeDeviceFromAllArc(this.id);
  }

  /**
   * 
   * @param key
   * @param value
   */
  private refreshGraphic(key: number, value: string | number | null): void {
    //core
    if (key == 0) this.core.setTitle(value.toString());
    if (key == 3 || key == 4) {
      let switchState = this.attrIndex2ValueMap.get(3);
      let enableState = this.attrIndex2ValueMap.get(4);
      if (switchState == "undefined" || enableState != "true")
        this.core.setState(1);
      else if (switchState == "opened") this.core.setState(2);
      else this.core.setState(0);
    }
  }

  /**
   * 
   * @param key
   * @param value
   */
  public setAttrValue(key: number, value: string | number | null): void {
    this.attrIndex2ValueMap.set(key, value);
    this.refreshGraphic(key, value);
    ArcManager.getInstance().setCurrentArcAttr(this.id, key, value);
  }

  /**
   * 
   */
  public refreshAllGraphic(): void {
    this.attrIndex2ValueMap.forEach((value, key) => {
      this.refreshGraphic(key, value);
    });
  }

  public get_AIV_Map(): AttrIndex2ValueMap {
    return this.attrIndex2ValueMap;
  }

  public set_AIV_Map(attrIndex2ValueMap: AttrIndex2ValueMap) {
    if (this.attrIndex2ValueMap)
      attrIndex2ValueMap.forEach((value, key) => {
        this.attrIndex2ValueMap.set(key, value);
      });
    else this.attrIndex2ValueMap = copyMap(attrIndex2ValueMap);

    this.refreshAllGraphic();
  }

  /**
   * ArcMode1
   * @returns {AIV_Map}
   */
  public pick_AIV_MapByArcMode(mode: number): AttrIndex2ValueMap {
    let targetMap: AttrIndex2ValueMap = new Map();
    this.attrIndex2ValueMap.forEach((value, key) => {
      let arcMode = Attribute.getArcMode(key);
      if (arcMode == mode) targetMap.set(key, value);
    });
    return targetMap;
  }
  static setAttribute(attrlist)
   {
     console.log(attrlist)
     Attribute.DEFAULT_ATTRS=attrlist.DEFAULT_ATTRS
     Attribute.setATTR_CLASSIFY(attrlist.ATTR_CLASSIFY)
   }
  /**
   * 
   * @returns {Attribute.Attrs}
   */
  public getAttrs(): Attrs {
    let attrs: Attrs = [];
    console.log(this.attrIndex2ValueMap)
    this.attrIndex2ValueMap.forEach((value, key) => {
      let attrData = Attribute.getDefaultAttrItem(key);
      attrData.value = value;
      attrs.push(attrData);
    });

    return attrs;
  }

  /**
   * 
   * @returns {Attribute.Attrs}
   */
  public getGcdwAttrs(id: number): Attrs {
    let attrs = [];
    this.attrIndex2ValueMap.forEach((value, key) => {
      let attrData = Attribute.getDefaultAttrItem(key);
      attrData.value = value;
      attrs.push(attrData);
    });
    return attrs;
  }

}


/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////---------------------------------------- OneNodeDevice------------------------------------------////
export class OneNodeDevice extends Device {
  static st_CLASS_NAME = "OneNodeDevice";

  //node1  2
  nodePosition: number;

  /**
   * OneNodeDevice
   * @param deviceType  
   * @param svgLayer  
   * @param np1  
   * @param np2  
   * @param id   ID
   * @param attrIndex2ValueMap ->Map
   */
  constructor(
    deviceType: DEVICE_TYPE,
    svgLayer: SvgLayer,
    np1: Node | Point,
    np2: Node | Point,
    id?: string | null,
    attrIndex2ValueMap?: AttrIndex2ValueMap | null
  ) {
    id =
      id == null
        ? DEVICE_TYPE[deviceType] + DEVICES.get(deviceType).lastID.toString()
        : id;

    let nodePosition = DEVICES.get(deviceType).nodePosition;

    let line = new Line(svgLayer, np1, np2, id);

    let points = [];
    points[1] = np1;
    points[2] = np2;

    let title: string = attrIndex2ValueMap
      ? attrIndex2ValueMap.get(0).toString()
      : null;

    let core = new Core(
      id,
      svgLayer,
      points[(nodePosition % 2) + 1],
      Device.computeRotate(np1, np2),
      DEVICE_TYPE[deviceType],
      {
        dx: -11,
        dy: -13,
        width: 26,
        height: 26
      },
      25,
      title
    );

    let node: Node;
    if (points[nodePosition] instanceof Node) {
      node = points[nodePosition] as Node;
      node.redrawAndLinkDevice(id, nodePosition);
    } else {
      node = new Node(svgLayer, points[nodePosition]);
      node.linkDevice(id, nodePosition);
    }

    let nodes = [];
    nodes[nodePosition] = node;
    super(id, deviceType, svgLayer, nodes, line, core, attrIndex2ValueMap);
    this.nodePosition = nodePosition;
  }

  /**
   * Node
   * @returns {Node} 
   */
  getNode(): Node {
    return this.nodes[this.nodePosition];
  }

  getNodeNum(): number {
    return 1;
  }

  getExtend(): Extend {
    return Device.st_getExtend(this.getNode(), this.core);
  }

  public doMouseDownLine() {
    super.doMouseDownLine();
  }

  public doMouseUpLine() {
    super.doMouseUpLine();
  }

  public doMouseDownCore() {
    let sm = StateManager.getInstance();
    if (
      sm.oneNodeDeviceSelected == this ||
      sm.oneNodeDeviceCoreSelected == this.core
    ) {
      sm.setMouseDownDeviceOrNode(this);
    } else {
      super.doMouseDownCore();
    }
  }

  public doMouseUpCore() {
    let sm = StateManager.getInstance();
    if (!sm.getType2draw() && !sm.isDeviceMoved) {
      //coreelementcore
      if (G.mouseEventBtn() == 1 && sm.oneNodeDeviceSelected == this) {
        this.selectCore(sm);
      }
      //
      else if (G.mouseEventBtn() == 2) {
        console.log("")
        //ElementContextMenu(G.X(), G.Y(), G.svgLayerActived);
        ContextMenu.st_popContextMenu(
          event,
          ContextMenu.st_createDeviceContextMenu()
        );
      }
      //corecorecore
      else {
        //OneNodeCore
        sm.clearAllSelect();
        this.select();
        sm.oneNodeDeviceSelected = this;
      }
    }
  }

  public moveNodeLinks(dx: number, dy: number, position: number) {
    this.line.movePure(dx, dy, position);
    let rotate = Device.computeRotate(this.line.p1, this.line.p2);
    this.core.movePure(0, 0, rotate);
  }

  public moveToNodeLinks(point: Point, position: number) {
    this.line.moveToPure(point, position);
    let rotate = Device.computeRotate(this.line.p1, this.line.p2);
    this.core.movePure(0, 0, rotate);
  }

  public moveCoreLinks(dx: number, dy: number) {
    this.line.movePure(dx, dy, (this.nodePosition % 2) + 1);
    let rotate = Device.computeRotate(this.line.p1, this.line.p2);
    this.core.movePure(0, 0, rotate);
  }

  public moveToCoreLinks(newCoordinate: Point) {
    this.line.moveToPure(newCoordinate, (this.nodePosition % 2) + 1);
    let rotate = Device.computeRotate(this.line.p1, this.line.p2);
    this.core.moveToPure(newCoordinate, rotate);
  }

  public add2nodeCoreMovingSet() {
    let set = StateManager.getInstance().nodeCoreMovingSet;
    set.add(this.getNode());
    set.add(this.core);
  }

  public getClassName(): string {
    return OneNodeDevice.st_CLASS_NAME;
  }

  public copy(
    nodeCopyMap: Map<string, Point>,
    deviceCopyArgsArray: Array<DeviceCopyArgs>
  ) {
    //nodeCopyMap
    let node = this.getNode();
    nodeCopyMap.set(node.id, { x: node.x, y: node.y }); //(+0)

    //deviceCopyArray
    let deviceType = this.deviceType,
      startPoint,
      endPoint;

    if (this.nodePosition == 1) {
      startPoint = node.id;
      endPoint = { x: this.core.x, y: this.core.y }; //
    } else {
      startPoint = { x: this.core.x, y: this.core.y }; //
      endPoint = node.id;
    }

    let copy: AttrIndex2ValueMap = copyMap(this.attrIndex2ValueMap);

    deviceCopyArgsArray.push({
      deviceType: deviceType,
      startPoint: startPoint,
      endPoint: endPoint,
      attrIndex2ValueMap: copy
    });
  }

  protected _remove(): void {
    let node = this.getNode();
    if (node.removeRecordFromDevicePosMapByID(this.id)) node = null;
    this.nodes = [];
    this.svgLayer.deviceMap.delete(this.id);
    this.core.remove();
    this.core = null;
    this.line.remove();
    this.line = null;
  }

  /**
   * DeviceCore
   * @param sm StateManager
   */
  private selectCore(sm: StateManager) {
    sm.clearAllAidGraphic();
    sm.oneNodeDeviceCoreSelected = this.core;
    sm.deviceSelectedArray.push(this);
    this.core.showAidGraphic();
  }

  getJsonObject(): {} {
    return {
      id: this.id,
      type: this.deviceType,
      layer: this.svgLayer.id,
      ndPos: this.nodePosition,
      p1: this.nodePosition == 1 ? this.getNode().id : null,
      p2: this.nodePosition == 2 ? this.getNode().id : null,
      cp: { x: this.core.x, y: this.core.y },
      rotate: this.core.rotate
    };
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////----------------------------------NodeDevice TwoNodeDevice------------------------------////
export class TwoNodeDevice extends Device {
  static st_CLASS_NAME = "TwoNodeDevice";
  static readonly DEFAULT_AID_GRAPHIC_ARG: AidGraphicArg = {
    dx: -23,
    dy: -14,
    width: 44,
    height: 27
  };

  /**
   * TwoNodeDevice
   * @param deviceType  
   * @param svgLayer  
   * @param np1  
   * @param np2  
   * @param id   ID
   * @param attrIndex2ValueMap ->Map
   */
  constructor(
    deviceType: DEVICE_TYPE,
    svgLayer: SvgLayer,
    np1: Node | Point,
    np2: Node | Point,
    id?: string | null,
    attrIndex2ValueMap?: AttrIndex2ValueMap | null
  ) {
    id =
      id == null
        ? DEVICE_TYPE[deviceType] + DEVICES.get(deviceType).lastID.toString()
        : id;

    let title: string = attrIndex2ValueMap
      ? attrIndex2ValueMap.get(0).toString()
      : null;

    let nodes: Node[] = [],
      line: Line = new Line(svgLayer, np1, np2, id),
      core: Core,
      textDy = DEVICES.get(deviceType).textDy;
    if (textDy)
      core = new Core(
        id,
        svgLayer,
        Device.computeCenterPosition(np1, np2),
        Device.computeRotate(np1, np2),
        DEVICE_TYPE[deviceType],
        TwoNodeDevice.DEFAULT_AID_GRAPHIC_ARG,
        textDy,
        title
      );
    else
      core = new Core(
        id,
        svgLayer,
        Device.computeCenterPosition(np1, np2),
        Device.computeRotate(np1, np2),
        DEVICE_TYPE[deviceType],
        TwoNodeDevice.DEFAULT_AID_GRAPHIC_ARG,
        25,
        title
      );

    //Node
    function innerFuc_buildNode(np: Node | Point, pos: number) {
      if (np instanceof Node) {
        nodes[pos] = np;
        np.redrawAndLinkDevice(id, pos);
      } else {
        nodes[pos] = new Node(svgLayer, np);
        nodes[pos].linkDevice(id, pos);
      }
    }

    innerFuc_buildNode(np1, 1);
    innerFuc_buildNode(np2, 2);

    super(id, deviceType, svgLayer, nodes, line, core, attrIndex2ValueMap);
  }

  getJsonObject(): {} {
    return {
      id: this.id,
      type: this.deviceType,
      layer: this.svgLayer.id,
      p1: this.nodes[1].id,
      p2: this.nodes[2].id,
      cp: { x: this.core.x, y: this.core.y },
      rotate: this.core.rotate
    };
  }

  getNodeNum(): number {
    return 2;
  }

  getExtend(): Extend {
    return Device.st_getExtend(this.nodes[1], this.nodes[2]);
  }

  public doMouseDownLine() {
    super.doMouseDownLine();
  }

  public doMouseUpLine() {
    super.doMouseUpLine();
  }

  public doMouseDownCore() {
    super.doMouseDownCore();
  }

  public doMouseUpCore() {
    super.doMouseUpCore();
  }

  public moveNodeLinks(dx: number, dy: number, position: number) {
    this.line.movePure(dx, dy, position);
    if (this.core) {
      let rotate = Device.computeRotate(this.line.p1, this.line.p2);
      this.core.movePure(Number(dx) / 2, Number(dy) / 2, rotate);
    }
  }

  public moveToNodeLinks(point: Point, pos: number) {
    this.line.moveToPure(point, pos);
    if (this.core) {
      let rotate = Device.computeRotate(this.line.p1, this.line.p2);
      this.core.moveToPure(
        Device.computeCenterPosition(this.line.p1, this.line.p2),
        rotate
      );
    }
  }

  public moveCoreLinks(dx: number, dy: number) {
    this.nodes[1].move(dx, dy, this.id);
    this.nodes[2].move(dx, dy, this.id);
    this.line.movePure(dx, dy);
  }

  public moveToCoreLinks(newCoordinate: Point) {
    this.nodes[1].moveTo(newCoordinate, this.id);
    this.nodes[2].moveTo(newCoordinate, this.id);
    //this.line.moveToPure(newCoordinate);
  }

  public add2nodeCoreMovingSet() {
    let set = StateManager.getInstance().nodeCoreMovingSet;
    set.add(this.nodes[1]);
    set.add(this.nodes[2]);
  }

  public getClassName(): string {
    return TwoNodeDevice.st_CLASS_NAME;
  }

  public copy(
    nodeCopyMap: Map<string, Point>,
    deviceCopyArgsArray: Array<DeviceCopyArgs>
  ) {
    //nodeCopyMap

    this.nodes.forEach(node => {
      if (node) nodeCopyMap.set(node.id, { x: node.x, y: node.y }); //
    });

    let attrIndex2ValueArrcopy: AttrIndex2ValueMap = new Map<
      number,
      string | number | null
    >();
    this.attrIndex2ValueMap.forEach((value, key) => {
      attrIndex2ValueArrcopy.set(key, value);
    });

    //deviceCopyArray
    deviceCopyArgsArray.push({
      deviceType: this.deviceType,
      startPoint: this.nodes[1].id,
      endPoint: this.nodes[2].id,
      attrIndex2ValueMap: attrIndex2ValueArrcopy
    });
  }

  protected _remove(): void {
    for (let i = 1; i < this.nodes.length; i++) {
      if (this.nodes[i])
        if (this.nodes[i].removeRecordFromDevicePosMapByID(this.id))
          this.nodes[i] = null;
    }

    this.nodes = [];
    this.svgLayer.deviceMap.delete(this.id);
    this.core.remove();
    this.core = null;
    this.line.remove();
    this.line = null;
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////----------------------------------NodeDevice ZeroNodeDevice------------------------------////
export class ZeroNodeDevice extends Device implements ContainSvgLayer {
  static st_CLASS_NAME = "ZeroNodeDevice";
  static readonly DEFAULT_AID_GRAPHIC_ARG: AidGraphicArg = {
    dx: -36,
    dy: -29,
    width: 72,
    height: 58
  };

  static st_doAfterDoubleClick: Function;

  static st_doAfterDelete: Function;

  //
  public innerLayer: SvgLayer;

  //
  private outNode4Relate: Node;

  //
  private innerNode4Relate: Node;

  //Map<outinner>
  private nodesRelateMap: Map<Node, Node>;

  /**
   * ZeroNodeDevice
   * @param deviceType  
   * @param svgLayer  
   * @param np1  
   * @param np2  
   * @param id   ID
   * @param attrIndex2ValueMap ->Map
   */
  //DEVICES.get(deviceType).attrDataIndexes
  constructor(
    deviceType: DEVICE_TYPE,
    svgLayer: SvgLayer,
    np1: Node | Point,
    np2: Node | Point,
    id?: string | null,
    attrIndex2ValueMap?: AttrIndex2ValueMap | null
  ) {
    id =
      id == null
        ? DEVICE_TYPE[deviceType] + DEVICES.get(deviceType).lastID.toString()
        : id;

    let core: Core,
      textDy = 40;

    let title: string = attrIndex2ValueMap
      ? attrIndex2ValueMap.get(0).toString()
      : null;

    core = new Core(
      id,
      svgLayer,
      Device.computeCenterPosition(np1, np2),
      0,
      DEVICE_TYPE[deviceType],
      ZeroNodeDevice.DEFAULT_AID_GRAPHIC_ARG,
      textDy,
      title
    );

    super(id, deviceType, svgLayer, [], null, core, attrIndex2ValueMap);

    this.nodesRelateMap = new Map();

    this.innerLayer = SvgLayerManager.getInstance().newSvgLayer(this);
  }

  getNodeNum(): number {
    return 0;
  }

  getExtend(): Extend {
    return new Extend(
      {
        x: this.core.x + ZeroNodeDevice.DEFAULT_AID_GRAPHIC_ARG.dx,
        y: this.core.y + ZeroNodeDevice.DEFAULT_AID_GRAPHIC_ARG.dy
      },
      {
        x: this.core.x - ZeroNodeDevice.DEFAULT_AID_GRAPHIC_ARG.dx,
        y: this.core.y - ZeroNodeDevice.DEFAULT_AID_GRAPHIC_ARG.dy
      }
    );
  }

  public doMouseDownLine() {
    throw new Error("doMouseDownLine");
  }

  public doMouseUpLine() {
    throw new Error("doMouseUpLine");
  }

  public doMouseDownCore() {
    super.doMouseDownCore();
  }

  public doMouseUpCore() {
    super.doMouseUpCore();
  }

  public moveNodeLinks(dx: number, dy: number, position: number) {
    throw new Error("moveNodeLinks");
  }

  public moveToNodeLinks(point: Point, pos: number) {
    throw new Error("moveToNodeLinks");
  }

  public moveCoreLinks(dx: number, dy: number) { }

  public moveToCoreLinks(newCoordinate: Point) { }

  public add2nodeCoreMovingSet() {
    let set = StateManager.getInstance().nodeCoreMovingSet;
    set.add(this.core);
  }

  public doDoubleClick() {
    this.openInnerLayer();
  }

  public getClassName(): string {
    return ZeroNodeDevice.st_CLASS_NAME;
  }

  public copy(
    nodeCopyMap: Map<string, Point>,
    deviceCopyArgsArray: Array<DeviceCopyArgs>
  ) {
    //deviceCopyArray

    let subNodeCopyMap: Map<string, Point> = new Map(),
      subDeviceCopyArray: Array<DeviceCopyArgs> = [];

    //device
    if (this.innerLayer) {
      this.innerLayer.deviceMap.forEach(device => {
        device.copy(subNodeCopyMap, subDeviceCopyArray);
      });
    }

    let attrIndex2ValueArrcopy: AttrIndex2ValueMap = new Map<
      ATTR_ENUM,
      string | number | null
    >();
    this.attrIndex2ValueMap.forEach((value, key) => {
      attrIndex2ValueArrcopy.set(key, value);
    });

    deviceCopyArgsArray.push({
      deviceType: this.deviceType,
      startPoint: { x: this.core.x, y: this.core.y },
      endPoint: { x: this.core.x, y: this.core.y },
      attrIndex2ValueMap: attrIndex2ValueArrcopy,
      subNodeCopyMap: subNodeCopyMap,
      subDeviceCopyArgsArray: subDeviceCopyArray
    });
  }

  /**
   * 
   */
  public openInnerLayer() {
    //svg
    if (this.innerLayer == null)
      this.innerLayer = SvgLayerManager.getInstance().newSvgLayer(this);

    //layer
    StateManager.getInstance().switchCurrentSvgLayer(this.innerLayer);

    //
    ZeroNodeDevice.st_doAfterDoubleClick(this.innerLayer);
  }

  public setInnerNode4relate(node: Node) {
    // if (this.innerNode4link)
    //     this.innerNode4link.removeLinkLayerContainer();
    this.innerNode4Relate = node;
    node.setRelatedDevice(this, true);
    this.refreshNodesRelateMap();
  }

  public setOutNode4relate(node: Node) {
    // if (this.outNode4link)
    //     this.outNode4link.removeLinkLayerContainer();
    this.outNode4Relate = node;
    //this.outNodelinkPro = node
    node.setRelatedDevice(this);
    this.refreshNodesRelateMap();
  }

  //
  protected _remove(): void {
    let innerLayId: string = this.innerLayer.id;

    if (this.innerLayer) {
      this.innerLayer.deviceMap.forEach(device => {
        device.remove();
      });
      this.innerLayer.deviceMap.delete(this.innerLayer.id);
    }
    this.svgLayer.deviceMap.delete(this.id);
    this.core.remove();
    this.core = null;
    this.innerLayer = null;

    ZeroNodeDevice.st_doAfterDelete(innerLayId);
  }

  private refreshNodesRelateMap() {
    if (this.outNode4Relate && this.innerNode4Relate) {
      this.outNode4Relate.setRelatedNode(this.innerNode4Relate);
      this.innerNode4Relate.setRelatedNode(this.outNode4Relate);
      this.nodesRelateMap.set(this.outNode4Relate, this.innerNode4Relate);
      this.outNode4Relate = null;
      this.innerNode4Relate = null;
    }
  }

  getJsonObject(): {} {
    return {
      id: this.id,
      type: this.deviceType,
      layer: this.svgLayer.id,
      cp: { x: this.core.x, y: this.core.y },
      rotate: this.core.rotate,
      innerSvg: this.innerLayer.id
    };
  }
}

